import {
  mapGetters,
  mapActions
} from 'vuex';
import F2 from '@antv/f2/lib/index-all';

export default {
  components: {

  },
  name: 'chart',
  data() {
    return {};
  },
  props: ['date'],
  methods: {
    ...mapActions([])
  },
  watch: {},
  computed: {
    ...mapGetters([])
  },
  created() {},
  mounted() {
    var data = this.date
    console.log(data)
    var chart = new F2.Chart({
      id: 'mountNode',
      pixelRatio: window.devicePixelRatio,
      height: document.querySelector('.chart-wrap').clientHeight,
      width: document.querySelector('.chart-wrap').clientWidth,
    });
    chart.source(data);
    chart.coord('polar', {
      transposed: true,
      radius: 0.9,
      innerRadius: 0.5
    });
    chart.axis(false);
    chart.legend(false);
    chart.tooltip(false);
    chart.guide().html({
      position: ['50%', '50%'],
      html: '<div style="text-align: center;width:150px;height: 50px;">\n      <p style="font-size: .34rem;color: #2e82ff;margin: 0;font-weight: bold;" id="title">总工时</p>\n      <p style="font-size: .34rem;color: #2e82ff;margin: 0;font-weight: bold;" id="money">200h</p>\n      </div>'
    });
    chart.interval().position('const*percent').adjust('stack').color('name', [data[0].color, data[1].color, data[2].color,
      data[3].color, data[4].color
    ]);
    chart.pieLabel({
      sidePadding: 30,
      activeShape: true,
      label1: function label1(data) {
        return {
          text: data.percent + '%',
          fill: '#343434',
          fontWeight: 'bold'
        };
      },
      label2: function label2(data) {
        return {
          text: data.name,
          fill: '#999'
        };
      },
      onClick: function onClick(ev) {
        var data = ev.data;
        if (data) {}
      }
    });
    chart.render();
  }
};
